/**
* @desc 
*    å¯å¨ç»ä»¶éæ¾æ¶èªå¨éæ¾ææçtimeoutè®¡æ¶å¨.
*     - å¨createdä¸­:             this.timer = new timer();
*     - å¨beforeDestroyä¸­:       this.timer.dispose();
*     - å¨ä½¿ç¨è®¡æ¶å¨çå½æ°ä¸­è°ç¨:   
                                var t = this.timer.setTimeout(fn, tm);
                                this.timer.clearTimeout(t);
                                this.timer.clearAllTimeout();
*     - å¨ä½¿ç¨å¸§å¨ç»çå½æ°ä¸­è°ç¨:   
                                var t = this.timer.requestAnimationFrame(fn);
                                this.timer.cancelAnimationFrame(t);
                                this.timer.clearAllAnimationFrame();
*     - ä½¿ç¨Promiseæ¹å¼å¯å¨è®¡æ¶å¨: (ä¼å¨å¯¹è±¡éæ¾åå®å¨åæ­¢)
                                this.timer.sleep(1000)
                                .then(()=>{
                                });
*/
export default class Timer {
  constructor() {
    this._timeoutList = [];
    this._requestAniList = [];
    this._intervalList = [];
  }

  dispose() {
    this.clearAll();
  }

  /**
  * @desc: Promiseæ¹å¼è®¡æ¶å¨.(ä¼å¨å¯¹è±¡éæ¾åå®å¨åæ­¢)
  * @return: Promise
  */
  sleep(ms) {
    var ctx = this;
    return new Promise(function (resolve, reject) {
      try {
        if (ms >= 0) {
          let timer = setTimeout(function(){
            let i = ctx._timeoutList.indexOf(timer);
            if (i >= 0)
              ctx._timeoutList.splice(i, 1);
            resolve();
          }, ms);
          ctx._timeoutList.push(timer);
        } else {
          resolve();
        }
      } catch(err) {
        reject(err);
      }
    });
  }

  /**
  * @desc: å¯å¨è®¡æ¶å¨.
  */
  setTimeout(fn, tm) {
    let ctx = this;
    let t = setTimeout(function() {
      fn();
      let i = ctx._timeoutList.indexOf(t);
      if (i >= 0)
        ctx._timeoutList.splice(i, 1);
    }, tm);
    if (t)
      this._timeoutList.push(t);
    return t;
  }

  /**
  * @desc: æ¸é¤è®¡æ¶å¨.
  */
  clearTimeout(t) {
    if (!t) return;
    clearTimeout(t);
    let i = this._timeoutList.indexOf(t);
    if (i >= 0)
      this._timeoutList.splice(i, 1);
  }

  /**
  * @desc: å¯å¨è®¡æ¶å¨.
  */
  setInterval(fn, tm) {
    let ctx = this;
    let t = setInterval(function() {
      fn();
    }, tm);
    if (t)
      this._intervalList.push(t);
    return t;
  }

  /**
  * @desc: æ¸é¤è®¡æ¶å¨.
  */
  clearInterval(t) {
    if (!t) return;
    clearInterval(t);
    let i = this._intervalList.indexOf(t);
    if (i >= 0)
      this._intervalList.splice(i, 1);
  }

  /**
  * @desc: å¯å¨å¸§å¨ç».
  */
  requestAnimationFrame(fn) {
    if (!fn) return null;
    let ctx = this;
    let t = requestAnimationFrame(function(tm){
      fn(tm);
      let i = ctx._requestAniList.indexOf(t);
      if (i >= 0)
        ctx._requestAniList.splice(i, 1);
    });
    if (t)
      this._requestAniList.push(t);
    return t;
  }

  /**
  * @desc: æ¸é¤å¸§å¨ç».
  */
  cancelAnimationFrame(t) {
    if (!t) return;
    cancelAnimationFrame(t);
    let i = this._requestAniList.indexOf(t);
    if (i >= 0)
      this._requestAniList.splice(i, 1);
  }

  /**
  * @desc: æ¸é¤ææè®¡æ¶å¨.
  */
  clearAllTimeout() {
    for(let t of this._timeoutList) {
      if (t) {
        clearTimeout(t);
      }
    }
    this._timeoutList = [];
  }

  /**
  * @desc: æ¸é¤ææè®¡æ¶å¨.
  */
  clearAllInterval() {
    for(let t of this._intervalList) {
      if (t) {
        clearInterval(t);
      }
    }
    this._intervalList = [];
  }
  
  /**
  * @desc: æ¸é¤ææå¸§å¨ç».
  */
  clearAllAnimationFrame() {
    for(let t of this._requestAniList) {
      if (t) {
        Timer.cancelAnimationFrame(t);
      }
    }
    this._requestAniList = [];
  }
  
  /**
  * @desc: æ¸é¤ææè®¡æ¶å¨ä¸å¸§å¨ç».
  */
  clearAll() {
    this.clearAllTimeout();
    this.clearAllAnimationFrame();
    this.clearAllInterval();
  }
}
